.confirm-board-delete {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    height: 250px;
    width: 450px;
    background-color: $colorBgBoardBars;
    z-index: 999;
    border-radius: 10px;
    box-shadow: 0px 0px 10px -5px #484444;

    @media (max-width:450px) {
        width: 95vw;
    }

    section {
        flex-direction: row-reverse;
    }

    p {
        color: $mainLightGrey;
    }

    button {
        border-radius: 5px;
        margin-left: 20px;
        font-size: 1.2rem;
    }

    .delete-button {
        background-color: #dd4444;
    }
}

.boardbar .modal-screen-wrapper {
    background-color: rgba(50, 50, 50, 0.5);
}