.my-week {
    position: relative;

    @media (max-width: 600px) {
        flex-direction: column;
    }

    .search-container {
        width: 95%;
        padding-left: 15px;
        padding-right: 50px;
    }

    .my-week-container {
        display: flex;
        flex-direction: column;
        padding: 0 50px;
        width: 100%;
        background-color: $colorBgBoardBars;
        height: 100vh;
        overflow-y: overlay;

        @media (max-width: 600px) {
            padding: 50px 15px 0;
            height: 92vh;
        }

        input {
            font-size: 1.2rem;
            color: $mainBlack;
            border-bottom-color: $mainLightGrey;

            &::placeholder {
                color: $mainLightGrey;
            }

            &:focus {
                background-color: $colorInputFieldFocus;
                border-bottom-color: $mainBlack;
            }
        }

        h1,
        h2,
        h3,
        h4,
        h5 {
            color: $mainBlack;
        }

        h1 {
            font-size: 1.9rem;


            @media(max-width: 600px) {
                font-size: 1.4rem;
            }
        }
    }

    .my-week-header {
        margin: 5vh 0px;
        height: 15vh;
        width: 500px;

        @media(max-width: 600px) {

            width: auto;
        }

        h1 {
            margin: auto;
        }

        img {
            height: 100px;
            width: 110px;
        }
    }

    .task-search {
        margin-bottom: 20px;
    }

    .upcoming-tasks {
        margin-bottom: 20px;

        .tasks-header {
            font-size: 1.875rem;
            font-weight: 400;
            margin-bottom: 15px;
        }
        
        .toggle-btn{  
            transition: .3s;
        }
         .upcoming-tasks-container{
            overflow: overlay;
            max-height: 250%;
            transition: max-height .4s ease-in-out;
            &.not-shown{  
                overflow: hidden;
                max-height: 0px; 
            } 
             .task-preview {
                 display: flex; 
                 height: 64px;
                 border-radius: 8px;
                 border: 1px solid #c5c7d0;
                 margin-bottom: 10px;
     
                 @media(max-width:600px) {
                     height: 50px;
                 }
     
                 .task-preview-name {
                     margin-bottom: 5px;
                 }
     
                 .left-column {
                     margin: 0 16px;
     
                     @media(max-width:600px) {
                         margin: 0 5px;
                     }
     
                     .task-location {
                         color: $mainLightGrey;
     
                         span {
                             cursor: pointer;
                             transition: .3s;
     
                             &:hover {
                                 text-decoration: underline;
                                 color: #4999c8;
                             }
                         }
                     }
     
                     .task-preview-name {
                         font-size: 1.125rem;
                         font-weight: 700;
                     }
                 }
     
                 .right-column {
                     height: 100%;
                     width: 200px;
                     border-bottom-right-radius: 8px;
                     border-top-right-radius: 8px;
     
                     @media (max-width: 600px) {
                         width: auto;
                     }
     
     
                     .user-img-container {
     
                         img,
                         .member-letter {
                             height: 35px;
                             width: 35px;
                             margin-right: 5px;
                         }
     
                         img {
                             border-radius: 50%;
                             cursor: pointer;
                             transition: .3s;
     
                             &:hover {
                                 box-shadow: $brightBoxShadow;
                             }
                         }
     
                         .task-number-of-imgs {
                             right: 15px;
                             height: 20px;
                             width: 20px;
                         }
                     }
     
                     .deadline-container {
                         width: 145px;
                         height: 100%;
                         justify-content: center;
                         background-color: #4999c8;
                         border-bottom-right-radius: 8px;
                         border-top-right-radius: 8px;
                         cursor: pointer;
                         transition: .3s;
     
                         @media (max-width: 600px) {
                             width: 70px;
                         }
     
                         h2 {
                             color: $colorBgBoardBars;
                         }
     
                         &:hover {
                             background-color: #3179a3;
                         }
                     }
                 } 
         }


        }
    }

    .greeting-container {
        width: 90%;
        margin-left: 50px;
    }

    .no-tasks {
        font-size: 3rem;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}