.task {
    display: flex;
    background-color: $taskBg;
    margin-bottom: 2px;
    height: 40px;
    border-radius: 1px;
    box-shadow: 0px 3px 4px -5px #484444;
    transition: background-color 0.3s;

    @media(max-width:600px) {
        width: 635px;
    }
 
    .content-editable {

        &:focus {
            max-width: 100%;
        }

        @media (max-width: 600px) {
            max-width: 20ch;
        }
    }

    &:hover {
        background-color: #e8e8e8;
    }

    svg {
        font-size: 2rem;
        color: $mainLightGrey;
        transition: .3s;

        &:hover {
            color: $buttonMainBg
        }
    }

    input {
        width: 100%;
        background-color: transparent;
        border: 0;
        padding-top: 7.5px;
        padding-bottom: 7.5px;
        border-radius: 1px;
        text-align: center;
        color: $inputColor;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    .task-remove-icon {
        font-size: 1.2rem;
    }

    .icon-container {
        margin-left: 5px;
    }

    *> {
        height: 100%;
    }

    .task-left {
        position: relative;
        height: 100%;
        overflow: hidden; 
        transition: 0.3s;

        @media (max-width: 380px) {
            margin-right: 5px;
        }

        &:hover {
            h2 {
                color: $buttonMainBg;
                
            }

            .notes-container {
                svg {
                    color: $buttonMainBg;
                }
            }
        }

        >* {
            height: 100%;
            display: flex;
            align-items: center
        }


        .task-color {
            width: 7px;
            margin-right: 1px;
            height: 100%;
        }

        .task-title-updates {
            cursor: pointer;
            max-width: 90%;
            h2 {
                margin-left: 15px;
                font-weight: 400;
                transition: 0.3s;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;

                @media(max-width:600px) {
                    margin-left: 0;
                }

                &:hover {
                    color: $buttonMainBg;
                }
            }

            .notes-container {
                text-align: end;

                .task-number-of-imgs {
                    font-family: monospace;
                    font-size: 10px;
                    width: 15px;
                    height: 14px;
                    position: absolute;
                    background-color: #0085ff;
                    color: #f2f1dfb0;
                    border-radius: 50%;
                    text-align: center;
                    right: -3px;
                    font-weight: bold;
                    bottom: 4px;
                    cursor: pointer;
                }
            }

        }
    }

    .task-right {
        height: 100%;

        > *:not(:first-child) {
            width: 120px;
            height: 100%; 
            display: flex;
            align-items: center;
            justify-content: center;
        }

        @media (max-width: 600px) {
            >* {
                width: 70px;
            }
        }

        .user-img-container {
            position: relative;

            .task-number-of-members {
                font-family: monospace;
                font-size: 12px;
                width: 18px;
                height: 18px;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                background-color: $buttonMainBg;
                color: $colorPlusCircleFont;
                border-radius: 50%;
                text-align: center;
                right: -5px;
                bottom: 10px;
                font-weight: bold;
                cursor: pointer;
            }

            img {
                cursor: pointer;
                border-radius: 50%;
                width: 35px;
                height: 35px;
                border: 2px solid #f1f1f1;
            }

            .member-img-container {

                >*:not(:first-child) {
                    margin-left: -10px;
                }
            }

            .no-members-icon-plus {
                font-family: monospace;
                font-size: 15px;
                width: 17px;
                height: 17px;
                position: absolute;
                background-color: #0085ff;
                color: #f2f1dfb0;
                border-radius: 50%;
                text-align: center;
                right: -5px;
                font-weight: bold;
                bottom: 10px;
                cursor: pointer;
            }

            .no-members-container {
                position: relative;

                .no-members-icon {
                    height: 30px;
                    width: 30px;
                }
            }

            .member-letter {
                cursor: pointer;
                border-radius: 50%;
                background: #e6e6e6;
                display: flex;
                justify-content: center;
                align-items: center;
                font-family: font1;
                color: $colorBgBoardBars;
                padding: 8px;
                background-color: $pink;
                font-weight: 700;
                width: 35px;
                height: 35px;
                border: 2px solid #f1f1f1;
            }

            .task-number-of-imgs {
                font-family: monospace;
                font-size: 15px;
                width: 17px;
                height: 17px;
                position: absolute;
                background-color: #0085ff;
                color: #f2f1dfb0;
                border-radius: 50%;
                text-align: center;
                right: 42px;
                font-weight: bold;
                bottom: 16px;
                cursor: pointer;
            }
        }

        .users-modal {
            background-color: $white;
            z-index: 999;
            width: 250px;
            border-radius: 5px;
            top: 35px;
            right: 0;
            padding: 0 10px;
            box-shadow: $modalsShadow;

            @media(max-width:600px) {
                position: fixed;
                top: auto;
            }

            .task-users-box {
                border-bottom: 1px solid #a8a8a87d;
                cursor: auto;

                svg {
                    font-size: 1.5rem;
                    color: $buttonMainBg;
                }

                .member-name {
                    margin-left: 10px;
                }
            }

            h3 {
                margin: 15px 0;
            }

            .user-box {
                margin-bottom: 10px;
            }

            .user-box-info {
                p {
                    text-align: left;
                    cursor: pointer;
                }
            }

            .board-users-box {
                cursor: auto;

                .member-name {
                    margin-left: 10px;
                }

                svg {
                    font-size: 1.5rem;
                    color: $buttonMainBg;
                }
            }
        }

        .label-container {
            height: 100%;

            .label-box {
                width: 100%;
                text-align: center;
                border-radius: 1px;
                cursor: default;
                font-size: 0.8rem;
                height: 100%;
                background-color: #a8a8a8;
                color: #f1f1f1;
            }

            .task-label-name {
                height: 100%;
                width: 100%;
                cursor: pointer;
                transition: 0.3s;

                &:hover {
                    box-shadow: $brightBoxShadow;
                }
            }

            .label-list {
                width: 150px;
                background-color: $white;
                box-shadow: $modalsShadow;
                z-index: 999;
                border-radius: 1px;
                right: 50px;
                top: 20px;
                word-break: break-all;
                padding: 5px 5px 0 5px;
                transition: 0.3s;

                @media(max-width:600px) {
                    position: fixed;
                    top: auto;
                }


                >*:hover {
                    box-shadow: $brightBoxShadow;
                }

                .label-selector {
                    width: 100%;
                    text-align: center;
                    height: 40px;
                    margin-bottom: 5px;
                    border-radius: 1px;
                    cursor: pointer;
                    background-color: $emptyStatusBg;
                    color: #f1f1f1;
                }
            }

            .stuck {
                background-color: #e44258 !important;
            }

            .working {
                background-color: #fdab3d !important;
            }

            .done {
                background-color: #00ca72 !important;
            }

            .low {
                background-color: #7bb0ae !important;
            }

            .medium {
                background-color: #a028c7 !important;
            }

            .high {
                background-color: #e82d2d !important;
            }
        }

        .react-datepicker__input-container {
            input {
                transition: 0.3s;

                &:hover,
                &:focus {
                    border: $dottedBorder;
                }
            }
        }
    }
}

.drag {
    background-color: #e8e8e8;
}