.user-profile {
  display: flex;

  .user-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: $colorBgBoardBars;

    h1,
    h2,
    h3,
    h4,
    h5 {
      color: $mainBlack;
    }

    hr {
      border: 0;
      box-shadow: 0px 0px 5px 0.5px #d2d2d2;
    }

    img {
      border-radius: 50px;
      object-fit: fill;
    }

    .header-container {
      width: 100%;
      background-color: $buttonMainBg;
      border-bottom: 1px solid rgba($colorFontTaskGroup, 0.35);
      min-height: 40vh;
    }
  }

  .user-details-container {
    min-height: 60vh;
    width: 100%;

    h2 {
      cursor: pointer;
      align-self: flex-start;
      text-decoration: underline;
      transition: 0.3s;

      &:hover {
        color: $buttonMainBg;
      }
    }

    .go-back-arrow {
      align-self: flex-start;
      margin-bottom: 20px;
      font-size: 15px;
      transition: 0.3s;
      top: 30px;

      &:hover {
        color: $buttonMainBg;
      }
    }

    .user-details-inner-container {
      display: flex;
      width: 100%;

      li {
        cursor: pointer;
        color: $buttonMainBg;
        line-height: 2;
        transition: 0.3s;

        &:hover {
          color: $buttonMainHover;
        }
      }

      > :not(:first-child) {
        margin-left: 50px;
      }

      .col-left {
        > h3:not(:first-child) {
          margin-top: 50px;
        }
      }

      .col-mid {
        > h3:not(:first-child) {
          margin-top: 50px;
        }

        .user-boards-container {
          margin-top: 10px;
          padding: 0 5px;
          max-height: 150px;
          overflow-y: overlay;

          li {
            min-height: 30px;
          }

          svg {
            font-size: 15px;
            margin-right: 5px;
            flex-shrink: 0;
          }
        }
      }

      .col-right {
        p {
          margin-top: 50px;
        }

        span {
          cursor: pointer;
          color: $buttonMainBg;
          transition: 0.3s;

          &:hover {
            color: $buttonMainHover;
          }
        }

        > h3:not(:first-child) {
          margin-top: 50px;
        }
      }

      @media (max-width: 1050px) {
        flex-direction: column;
        margin-top: 50px;
        text-align: center;

        > :not(:first-child) {
          margin-left: 0;
          margin-top: 50px;
        }
        ::-webkit-scrollbar {
          display: initial;
        }
      }
    }

    @media (max-width: 600px) {
      height: auto;

      hr {
        border: 0;
        height: 1px;
        background-image: linear-gradient(
          to right,
          rgba(0, 0, 0, 0),
          rgba(0, 0, 0, 0.75),
          rgba(0, 0, 0, 0)
        );
      }
    }
  }
}
